import React from "react";
import Layout from "../components/layout";
import { graphql, Link } from "gatsby";
import { css } from "@emotion/core";
import Img from 'gatsby-image';

export default ({ data, pageContext }) => {
  const { tag } = pageContext;
  const tagEdges = data.allMarkdownRemark.edges.filter(
    ({ node }) => node.frontmatter.tags.includes(tag)
  );

  return (
    <Layout>
      <h2 css={css`border: solid 2px #000;text-align: center;margin: 20px;padding: 10px;`}>{tag}</h2>
        {tagEdges.map(({ node }, index) =>
         <div key={index} css={css`padding: 20px; background-color: #ccc;border-bottom: solid 1px #bbb;`}>
            <Link to={node.fields.slug}  css={css`color:#333;text-decoration: none;`}>
     <div css={css`display: flex;`} >      
<div css={css`margin-right:15px;`}>
            <Img fixed={node.frontmatter.thumbnail.childImageSharp.fixed}/>
</div>   
<div css={css`margin-block-end: 0;`}>         
              <h3 css={css`margin-block-end: 0;margin-block-start: 0;`}>{node.frontmatter.title}</h3>
              <p css={css`margin-block-start: 0.6em;font-size: 15px;`}>{node.excerpt}</p>
              <p css={css`margin-block-start: 0.6em;font-size: 13px;`}>{node.frontmatter.date}</p>
</div>               
    </div>               
            </Link>
                </div>
        )}
    </Layout>
  )
}

export const query = graphql`
  query {
    allMarkdownRemark(
        sort: {order: DESC, fields: [frontmatter___date] }
        limit: 1000
      ) {
      edges {
        node {
          frontmatter {
            title
            date(formatString: "DD/MM/YYYY")
            tags
            thumbnail {
              childImageSharp {
                fixed(width: 300) {
                  ...GatsbyImageSharpFixed
                }
              }
            }
          }
          excerpt
          fields {
            slug
          }
        }
      }
    }
  }
`